.feedback {
  width: 100vw;
  height: 100vh;
  padding: 24px 16px;
  display: flex;
  flex-direction: column;
  position: fixed;
  z-index: 6;
  top: 0;
  right: 0;
  border: 1px solid #D9FFF5;
  border-radius: 1px;
  transition: width 1s, transform 2s;
  transform: translateX(130%);
  background-color: #FFFFFF; }

.feedback-translate {
  transform: translateX(0%); }

.feedback__close-btn {
  margin-right: auto;
  background-color: transparent;
  border: none;
  margin-bottom: 40px; }

.feedback__title {
  display: flex;
  align-items: center;
  font-family: 'TT Lakes Bold', sans-serif;
  color: #1B1C21;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.6px;
  margin-bottom: 24px; }

.feedback__title::before {
  content: '';
  background-image: url(./static/highlight.svg);
  background-color: #41F6D7;
  border-radius: 0 10px 10px 0;
  width: 4px;
  height: 40px;
  position: absolute;
  left: 0;
  display: inline-block; }

.feedback__form {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 16px; }

.feedback__form-name,
.feedback__form-phone,
.feedback__form-email,
.feedback__form-text {
  width: 100%;
  font-family: 'TT Lakes Medium', sans-serif;
  color: #B5B6BC;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  border-radius: 8px;
  border: 1px solid #EAEAEA;
  padding: 12px 16px; }

.feedback__agreement {
  font-family: 'TT Lakes Medium', sans-serif;
  color: #7E7E82;
  font-size: 14px;
  line-height: 16px;
  margin-bottom: 19px; }

.feedback__agreement span {
  color: #FF3E79; }

.feedback__send-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 136px;
  height: 32px;
  background: #FF3E79;
  border-radius: 16px;
  margin-left: auto; }

.feedback__send-btn:hover {
  transform: scale(0.95);
  transition: transform 0.2s ease-in-out;
  opacity: 0.5; }

.feedback__send-btn-text {
  font-family: 'TT Lakes Bold', sans-serif;
